import React, { Component } from 'react';
import './App.css';
var uuid = require('uuid');
var firebase = require('firebase');

var config = {
    apiKey: "AIzaSyALEMGO2u4Z_tIT_P-LdVuP6CJ4XTYxzcg",
    authDomain: "simplesurvey-82394.firebaseapp.com",
    databaseURL: "https://simplesurvey-82394.firebaseio.com",
    storageBucket: "simplesurvey-82394.appspot.com",
    messagingSenderId: "662440369797"
  };
  firebase.initializeApp(config);

class App extends Component {

  constructor(props){
    super(props);
    this.state = {
      id: uuid.v1(),
      name: '',
      answers: {
        q1: '',
        q2: '',
        q3: '',
        q4: '',
      },
      submitted: false
    }

    this.handleQuestionChange = this.handleQuestionChange.bind(this);
  }

  handleNameSubmit(event){
    event.preventDefault();

    var name = this.refs.name.value;
    this.setState({name: name}, function() {
      console.log(this.state);
    });
  }

  handleQuestionSubmit(event){
    event.preventDefault();


    firebase.database().ref('surveys/'+this.state.id).set({
      name: this.state.name,
      answers: this.state.answers
    });

    this.setState({submitted: true}, function (){
      console.log(console.log('Questions Submitted'))
    });
  }

  handleQuestionChange(event){

    var answers = this.state.answers;
    if(event.target.name === 'q1'){
      answers.q1 = event.target.value;
    } else if (event.target.name === 'q2'){
      answers.q2 = event.target.value;
    } else if (event.target.name === 'q3'){
      answers.q3 = event.target.value;
    } else if (event.target.name === 'q4'){
      answers.q4 = event.target.value;
    }

    this.setState({answers: answers}, function() {
      console.log(this.state);
    });
  }

  render() {

    var user;
    var questions;

    if(this.state.name && this.state.submitted === false){

      user = <h2> Welcome {this.state.name}! </h2>;

      questions = <span>
        <h3>Survery Questions</h3>
        <form onSubmit={this.handleQuestionSubmit.bind(this)}>
          <div>
            <label>What is your favorite desktop operating system?</label> <br />
            <input type="radio" name="q1" value="Windows" onChange={this.handleQuestionChange} />Windows <br />
            <input type="radio" name="q1" value="OS X" onChange={this.handleQuestionChange} />OS X <br />
            <input type="radio" name="q1" value="Linux" onChange={this.handleQuestionChange} />Linux <br />
            <input type="radio" name="q1" value="Solaris" onChange={this.handleQuestionChange} />Solaris <br />
            <input type="radio" name="q1" value="Other" onChange={this.handleQuestionChange} />Other <br />
          </div>

          <div>
            <label>What is your favorie mobile operating system?</label> <br />
            <input type="radio" name="q2" value="iOS" onChange={this.handleQuestionChange} />iOS <br />
            <input type="radio" name="q2" value="Android" onChange={this.handleQuestionChange} />Android <br />
            <input type="radio" name="q2" value="Windows Phone" onChange={this.handleQuestionChange} />Windows Phone <br />
            <input type="radio" name="q2" value="Other" onChange={this.handleQuestionChange} />Other <br />

          </div>

          <div>
            <label>What is your favorite smartphone manufacturer?</label> <br />
            <input type="radio" name="q3" value="Apple" onChange={this.handleQuestionChange} />Apple <br />
            <input type="radio" name="q3" value="Samsung" onChange={this.handleQuestionChange} />Samsung <br />
            <input type="radio" name="q3" value="LG" onChange={this.handleQuestionChange} />LG <br />
            <input type="radio" name="q3" value="HTC" onChange={this.handleQuestionChange} />HTC <br />
            <input type="radio" name="q3" value="Other" onChange={this.handleQuestionChange} />Other <br />
          </div>

          <div>
            <label>What is your favorite music app?</label> <br />
            <input type="radio" name="q4" value="Pandora" onChange={this.handleQuestionChange} />Pandora <br />
            <input type="radio" name="q4" value="Spotify" onChange={this.handleQuestionChange} />Spotify <br />
            <input type="radio" name="q4" value="Apple Music" onChange={this.handleQuestionChange} />Apple Music <br />
            <input type="radio" name="q4" value="Google Play Music" onChange={this.handleQuestionChange} />Google Play Music <br />
            <input type="radio" name="q4" value="Other" onChange={this.handleQuestionChange} />Other <br />
          </div>

          <input type="submit" value="Submit" /> <br /> <br />

        </form>
      </span>

    } else if (!this.state.name && this.state.submitted === false){

      user = <span>
        <h2>Please enter your name to begin the survery</h2>
        <form onSubmit={this.handleNameSubmit.bind(this)}>
          <input type="text" placeholder="Enter Name..." ref="name" />
        </form>
      </span>;

      questions = '';

    } else if (this.state.submitted === true){
      user = <h2>Thank You {this.state.name}!</h2>;
    }

    return (
      <div className="App">
        <div className="App-header text-center">
          <h2>Simple Survey</h2>
        </div>

        <div className="text-center">
          {user}
        </div>

        <div className="container">
          {questions}
        </div>
      </div>
    );
  }
}

export default App;



